.mgui-avatar {
  display: inline-block;
  vertical-align: top;
  position: relative;
  font-size: 40px;
  line-height: 1;
}
.mgui-avatar-img {
  overflow: hidden;
  border-radius: 50em;
  width: 1em;
  vertical-align: top;
  line-height: 1;
}
.mgui-avatar-badge {
  position: absolute;
  right: 0;
  bottom: 0;
  background: #b2b2b2;
  width: 15px;
  height: 15px;
  border: solid 2px #fff;
  border-radius: 15px;
}
.mgui-avatar-badge.type-away {
  background: #b2b2b2;
}
.mgui-avatar-badge.type-online {
  background: #0084ff;
}
.mgui-bubble {
  display: inline-block;
  vertical-align: top;
  border-radius: 20px;
  text-align: left;
  margin-bottom: 0.2em;
  overflow: hidden;
  line-height: 1;
  background: #f1f0f0;
  max-width: 85%;
  margin-bottom: 0.2em;
}
.mgui-bubble-text {
  padding: 0.5em 0.8em;
  line-height: 1.4;
}
.mgui-bubble-image {
  max-width: 100%;
}
.mgui-bubble.is-our {
  background: #0084ff;
  color: #fff;
}
.mgui-bubbleGroup {
  position: relative;
  margin: 0.3em 0;
}
.mgui-bubbleGroup:after {
  content: '';
  display: block;
  clear: both;
}
.mgui-bubbleGroup.has-avatar {
  padding-left: 48px;
}
.mgui-bubbleGroup-avatar {
  position: absolute;
  bottom: 0.3em;
  left: 0;
}
.mgui-bubbleGroup-avatar .avatar {
  font-size: 40px;
}
.mgui-bubbleGroup-elements > * {
  clear: both;
}
.mgui-bubbleGroup.is-their .mgui-bubbleGroup-elements > * {
  float: left;
}
.mgui-bubbleGroup.is-their .mgui-bubbleGroup-elements > *:not(:first-child) {
  border-top-left-radius: 6px;
}
.mgui-bubbleGroup.is-their .mgui-bubbleGroup-elements > *:not(:last-child) {
  border-bottom-left-radius: 6px;
}
.mgui-bubbleGroup.is-our .mgui-bubbleGroup-elements > * {
  float: right;
}
.mgui-bubbleGroup.is-our .mgui-bubbleGroup-elements > *:not(:first-child) {
  border-top-right-radius: 6px;
}
.mgui-bubbleGroup.is-our .mgui-bubbleGroup-elements > *:not(:last-child) {
  border-bottom-right-radius: 6px;
}
.mgui-button {
  display: block;
  text-decoration: none;
  padding: 0.7em;
  color: #0084ff;
  cursor: pointer;
}
.mgui-button.is-disabled {
  color: #b2b2b2;
  cursor: default;
}
.mgui-buttonsBubble {
  display: inline-block;
  vertical-align: top;
  border-radius: 20px;
  text-align: left;
  margin-bottom: 0.2em;
  overflow: hidden;
  line-height: 1;
  background-color: #fff;
  border: solid 1px #f1f0f0;
}
.mgui-buttonsBubble-text {
  padding: 0.5em 0.8em 0.6em;
  background: #f1f0f0;
}
.mgui-buttonsBubble-buttons .mgui-button {
  text-align: center;
  border-top: solid 1px #f1f0f0;
}
.mgui-genericElement {
  display: inline-block;
  vertical-align: top;
  border-radius: 20px;
  text-align: left;
  margin-bottom: 0.2em;
  overflow: hidden;
  line-height: 1;
  background-color: #fff;
  width: 15em;
  max-width: 80vw;
  margin-bottom: 0.3em;
  border: solid 1px #ebebeb;
  display: flex;
  flex-direction: column;
}
.mgui-genericElement:not(:last-child) {
  margin-right: 0.1em;
}
.mgui-genericElement-image {
  height: 0;
  padding-top: 52.35602%;
  background-position: center center;
  background-size: cover;
}
.mgui-genericElement-text {
  padding: 0.5em 0.75em;
  font-size: 0.95em;
  line-height: 1.4;
  flex-grow: 1;
}
.mgui-genericElement-title {
  color: #000;
  font-weight: 600;
}
.mgui-genericElement-subtitle {
  color: #818181;
}
.mgui-genericElement-itemUrl {
  color: #b2b2b2;
}
.mgui-genericElement-buttons .mgui-button {
  border-top: solid 1px #ebebeb;
  text-align: center;
  flex-grow: 0;
}
.mgui-genericElement:not(:first-child) {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.mgui-genericElement:not(:last-child) {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.mgui-genericElementList {
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  white-space: nowrap;
  display: flex;
}
.mgui-genericElementList > * {
  white-space: initial;
  flex-shrink: 0;
}
.mgui-persistentMenu {
  background: #fff;
}
.mgui-persistentMenu-head {
  background: #f9f9f9;
  border-bottom: solid 1px rgba(0,0,0,0.15);
  text-align: center;
  position: relative;
}
.mgui-persistentMenu-title,
.mgui-persistentMenu-closeBtn {
  padding: 0.5em;
}
.mgui-persistentMenu-title {
  font-weight: 600;
}
.mgui-persistentMenu-closeBtn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  color: #037aff;
}
.mgui-persistentMenu-elements {
  padding: 0.2em 0;
}
.mgui-persistentMenu-element .mgui-button {
  display: block;
  text-align: center;
  padding: 0.4em;
}
.mgui-screen .mgui-persistentMenu {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.mgui-quickReply {
  display: inline-block;
  vertical-align: top;
  border-radius: 20px;
  text-align: left;
  margin-bottom: 0.2em;
  overflow: hidden;
  line-height: 1;
  color: #0084ff;
  border: solid 1px;
  cursor: pointer;
  white-space: nowrap;
}
.mgui-quickReply-icon {
  display: inline-block;
  width: 1.35em;
  height: 1.35em;
  margin: 0.3em;
  margin-left: 0.35em;
  box-sizing: border-box;
  border-radius: 1em;
  vertical-align: top;
  margin-right: -0.5em;
  background: #0084ff;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.mgui-quickReply-icon.is-location {
  background: #0084ff url("data:image/svg+xml;base64,") no-repeat center center;
  background-size: 100%;
}
.mgui-quickReply-title {
  display: inline-block;
  padding: 0.5em 0.75em;
}
.mgui-quickReply.is-hover,
.mgui-quickReply:hover {
  background: #0084ff;
  color: #fff;
}
.mgui-quickReplyList {
  text-align: center;
  width: 100%;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}
.mgui-receipt {
  display: inline-block;
  vertical-align: top;
  border-radius: 20px;
  text-align: left;
  margin-bottom: 0.2em;
  overflow: hidden;
  line-height: 1;
  background-color: #fff;
  border: solid 1px #ebebeb;
  width: 15em;
  max-width: 90vw;
  margin-bottom: 0.3em;
  padding: 0 0.75em;
}
.mgui-receipt-head,
.mgui-receipt-body,
.mgui-receipt-foot {
  padding: 0.75em 0;
}
.mgui-receipt-head {
  border-bottom: solid 1px #ebebeb;
  color: #929292;
}
.mgui-receipt-body {
  font-size: 0.95em;
  line-height: 1.4;
}
.mgui-receipt-foot {
  border-top: solid 1px #ebebeb;
}
.mgui-receipt-foot-label {
  float: left;
  color: #929292;
}
.mgui-receipt-foot-sum {
  float: right;
  font-weight: 600;
}
.mgui-receipt-foot:after {
  display: block;
  content: '';
  clear: both;
}
.mgui-receipt-key {
  margin-top: 0.5em;
  color: #929292;
}
.mgui-receipt-value {
  font-weight: 600;
}
.mgui-receiptElement {
  margin-bottom: 0.3em;
}
.mgui-receiptElement:after {
  display: block;
  content: '';
  clear: both;
}
.mgui-receiptElement-image {
  width: 5em;
  height: 5.5em;
  background-size: cover;
  background-position: center center;
  border: solid 1px #ebebeb;
  float: left;
  margin-right: 0.5em;
}
.mgui-receiptElement-desc {
  line-height: 1.2;
}
.mgui-receiptElement-title {
  font-weight: 600;
}
.mgui-receiptElement-subtitle {
  color: #929292;
  font-size: 0.95em;
  margin-top: 0.1em;
}
.mgui-screen {
  display: inline-block;
  width: 375px;
  max-width: 100%;
  height: 520px;
  background: #fff;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
}
.mgui-screen.with-shadow {
  box-shadow: 0 1px 2px rgba(0,0,0,0.2), 0 2px 10px rgba(0,0,0,0.2);
}
.mgui-screenConversation {
  padding: 0.5em;
  max-height: 100%;
  overflow-y: auto;
}
.mgui-screenOverlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.2);
}
.mgui-threadList {
  padding: 0.75em;
}
.mgui-threadListElement {
  position: relative;
  margin-bottom: 0.5em;
  line-height: 1;
}
.mgui-threadListElement-avatar {
  position: absolute;
  top: 0;
  left: 0;
}
.mgui-threadListElement-avatar .mgui-avatar {
  display: block;
  font-size: 56px;
}
.mgui-threadListElement-info {
  padding-top: 0.5em;
  min-height: 56px;
  margin-left: 67.2px;
  position: relative;
  line-height: 1.4;
}
.mgui-threadListElement-lead {
  color: #929292;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.9em;
  margin-right: 2em;
}
.mgui-threadListElement-timestamp {
  float: right;
  font-size: 0.8em;
  padding-top: 0.3em;
}
.mgui-threadListElement-status {
  position: absolute;
  top: 2.4em;
  right: 0;
  width: 0.5em;
  height: 0.5em;
  background: #ebebeb;
  border-radius: 0.5em;
}
.mgui-threadListElement.is-unread .mgui-threadListElement-label,
.mgui-threadListElement.is-unread .mgui-threadListElement-lead {
  font-weight: 600;
  color: #000;
}
.mgui-threadListElement.is-unread .mgui-threadListElement-timestamp .mgui-timestamp {
  color: #0084ff;
}
.mgui-timestamp {
  color: #b2b2b2;
}
.mgui-timestamp.view-block {
  display: block;
  text-align: center;
  margin: 0.5em 0;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
@-ms-viewport {
  width: device-width;
}
html {
  font-size: 16px;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
}
[tabindex="-1"]:focus {
  outline: none !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}
p {
  margin-top: 0;
  margin-bottom: 1rem;
}
abbr[title],
abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted #333;
}
address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}
ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}
ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}
dt {
  font-weight: bold;
}
dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}
blockquote {
  margin: 0 0 1rem;
}
a {
  color: #11e;
  text-decoration: none;
}
a:not([href]) {
  color: inherit;
  text-decoration: none;
}
a:not([href]):focus {
  outline: none;
}
pre {
  margin-top: 0;
  margin-bottom: 1rem;
}
figure {
  margin: 0 0 1rem;
}
img {
  vertical-align: middle;
}
button,
[type="submit"],
[type="reset"],
[type="button"],
[role="button"] {
  cursor: pointer;
}
a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}
table {
  background-color: transparent;
}
caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #777;
  text-align: left;
  caption-side: bottom;
}
th {
  text-align: left;
}
label {
  display: inline-block;
  margin-bottom: 0.5rem;
}
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}
input,
button,
select,
textarea {
  margin: 0;
  line-height: inherit;
  border-radius: 0;
}
textarea {
  resize: vertical;
}
fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}
legend {
  display: block;
  width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
}
input[type="search"] {
  -webkit-appearance: none;
}
output {
  display: inline-block;
}
[hidden] {
  display: none !important;
}
@media (min-width: 700px) {
  .guide-color-list .guide-color {
    width: 50%;
    float: left;
  }
}
@media (min-width: 1000px) {
  .guide-color-list .guide-color {
    width: 33.333%;
  }
}
.guide-color {
  margin-bottom: 1em;
}
.guide-color-swatch {
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: solid 1px rgba(0,0,0,0.1);
  border-radius: 0.2em;
  overflow: hidden;
}
.guide-color-tint {
  height: 0;
  padding-top: 0.75em;
  border-bottom: solid 1px rgba(0,0,0,0.1);
}
.guide-color-hex {
  text-transform: uppercase;
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  padding: 0.2em 0.5em;
}
.guide-color-title {
  display: inline-block;
  vertical-align: middle;
  font-size: 1.2em;
  margin-left: 1em;
}
body {
  cursor: default;
  background: #fff;
}
.guide {
  max-width: 1000px;
  margin: 0 auto;
}
.guide-head {
  text-align: center;
}
@media (min-width: 400px) {
  .guide {
    padding: 2em;
    padding-bottom: 4em;
  }
}
.section a {
  color: inherit;
}
.section:not(:first-child) {
  margin-top: 2em;
  border-top: solid 1px #eee;
  padding-top: 1.5em;
}
.section-block:after {
  display: block;
  content: '';
  clear: both;
}
